import React, { useRef, useEffect } from 'react';
import draw from './draw';

// CONSISTENT SPEED ISSUE
// Be sure to always use the first argument (or some other method for getting 
//   the current time) to calculate how much the animation will progress in a 
//   frame, otherwise the animation will run faster on high refresh rate screens. 
//   Check the example below for a way to do this.

// IMAGE ISSUE
// var img = new Image();
// img.setAttribute('crossOrigin', 'anonymous');
// img.src = source;
// ctx.drawImage(img, 0, 0);
// console.log(ctx);

function Animation(props) {
  const { conditions, width, height } = props;
  const canvasReference = useRef(null);

  useEffect(() => {
    var ctx = canvasReference.current.getContext('2d');
    var animationFrameId;
    var frameCount = 0;
    function render() {
      frameCount++;
      draw(ctx, frameCount, conditions, width, height);
      animationFrameId = window.requestAnimationFrame(render);
    }
    render();

    return () => { window.cancelAnimationFrame(animationFrameId) };
  }, [conditions, width, height]);

  return (
    <canvas
      className="border"
      ref={canvasReference}
      width={width}
      height={height}
    />
  );
}

export default Animation;
