import React, { Component } from 'react';
import Animation from './Animation';
import Form from './Form';
import './App.css';

// VALIDITY ISSUE
// evaluateEqValidity()

// SUBMIT EMPTY FORM ISSUE
// Empty OP or Empty EQ

// IMAGE ISSUE
// imgSrc: "https://www.visitaparadise.com/wp-content/themes/yootheme/cache/sunset-d863fdd4.jpeg"

class App extends Component {
  constructor() {
    super();
    this.eqToString = this.eqToString.bind(this);
    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
    this.evaluateEqValidity = this.evaluateEqValidity.bind(this);
    this.generateEqComponents = this.generateEqComponents.bind(this);
    const eqDefault = [
      {
        op: null,
        eq: "Math.sin(x*Math.pow(t, 0.2)) > Math.cos(y*Math.pow(t, 0.2))"
      },
      {
        op: "&&",
        eq: "x*x + y*y < t*t"
      }
    ];
    this.state = {
      width: Math.min(window.innerWidth - 8, window.innerHeight - 8),
      height: Math.min(window.innerWidth - 8, window.innerHeight - 8),
      eqInput: "",
      opInput: "",
      eq: eqDefault,
      eqComponents: this.generateEqComponents(eqDefault),
      conditions: new Function("x", "y", "t", "return " + this.eqToString(eqDefault)),
      eqValid: true
    };
  }

  eqToString(eq) {
    var eqString = "";
    for (const item of eq) {
      if (item.op)
        eqString += item.op;
      eqString += item.eq;
    }
    return eqString;
  }

  evaluateEqValidity() {
    try {
      new Function("x", "y", "t", "return " + this.state.eqInput);
    } catch (error) {
      this.setState({ eqValid: false });
    }
    this.setState({ eqValid: true });
  }

  generateEqComponents(eqNew) {
    return eqNew.map((item) => {
      console.log(item);
      var eqComp = "";
      if (item.op)
        eqComp += item.op === '&&' ? "AND " : "OR  ";
      eqComp += item.eq;
      return (
        <div key={item.eq}>
          <button
            name="remove"
            value={item.eq}
            onClick={this.handleChange}> X </button>
          <span>{eqComp}</span>
        </div>
      );
    });
  }

  handleChange(event) {
    const { name, value } = event.target;
    if (name === "remove") {
      var eqNew = [];
      this.state.eq.forEach((item) => {
        if (item.eq !== value)
          eqNew.push(item);
      });
      if (eqNew[0] && eqNew[0].op)
        eqNew[0].op = null;
      this.setState({
        eq: eqNew,
        eqComponents: this.generateEqComponents(eqNew),
        conditions: new Function("x", "y", "t", "return " + this.eqToString(eqNew)),
      });
    } else
      this.setState({ [name]: value });
  }

  handleSubmit(event) {
    event.preventDefault();
    this.evaluateEqValidity();
    if (this.state.eqValid) {
      const eqNext = {
        id: this.state.eq.length,
        op: this.state.opInput,
        eq: this.state.eqInput
      };
      const eqNew = this.state.eq.concat(eqNext);
      this.setState({
        eq: eqNew,
        eqComponents: this.generateEqComponents(eqNew),
        conditions: new Function("x", "y", "t", "return " + this.eqToString(eqNew)),
      });
    }
  }

  render() {
    return (
      <div>
        <Form
          handleChange={this.handleChange}
          handleSubmit={this.handleSubmit}
          eqComponents={this.state.eqComponents}
          eqInput={this.state.eqInput}
          eqValid={this.state.eqValid}
        />

        <br />
        <Animation
          conditions={this.state.conditions}
          width={this.state.width}
          height={this.state.height}
        />
      </div>
    );
  }

};

export default App;

