import React from 'react';

function Form(props) {
    const { handleChange, handleSubmit, eqComponents, eqInput, eqValid } = props;

    return (
        <div>
            <form onSubmit={handleSubmit}>
                <input
                    type="text"
                    name="eqInput"
                    value={eqInput}
                    onChange={handleChange}
                />
                <button> Add </button>
                <br />
                <input
                    type="radio"
                    name="opInput"
                    value="&&"
                    onChange={handleChange}
                /> AND
            <br />
                <input
                    type="radio"
                    name="opInput"
                    value="||"
                    onChange={handleChange}
                /> OR
            <span style={{ display: eqValid ? "none" : "block" }} > invalid eq syntax </span>
            </form>
            {eqComponents}
        </div>
    );
}

export default Form;
