
// IMAGE ISSUE

// OPTION FOR RECYCLE CANVAS ISSUE
// const imgData = ctx.getImageData(0, 0, ctx.canvas.width, ctx.canvas.height);

function draw(ctx, frameCount, conditions, width, height) {
    const imgData = ctx.createImageData(ctx.canvas.width, ctx.canvas.height);
    var pixels = imgData.data;

    var x, y, xc = 0, yc = 0;
    for (var i = 0; i < pixels.length; i += 4) {
        x = xc + 1 - width / 2;
        y = height / 2 - yc;

        const tmax = 150;
        const k = frameCount % (2 * tmax);
        var t;
        if (k < tmax) t = k;
        else t = 2 * tmax - k - 1;

        if (conditions(x, y, t)) {
            pixels[i] = (3 * t) % 256;
            pixels[i + 1] = (2 * t) % 256;
            pixels[i + 2] = (5 * t) % 256;
        }
        pixels[i + 3] = 255;

        xc++;
        if (xc === width) {
            xc = 0;
            yc++;
        }
    }

    ctx.putImageData(imgData, 0, 0);
}

export default draw;